.Coures-card-container{
    margin-top: 2rem;
    padding: 2rem;
    border: 1px solid rgb(43, 37, 37);


    h1{
        font-size: 20px;
        padding: 0;
    }
    p{
        padding: 0;
        padding-bottom: 1rem;
        width: 70%;
        font-size: 15px;
    }
    
    a{
        
        padding: .5rem;
        border: 1px solid rgb(38, 22, 22);
        
        &:hover{
            background-color: rgb(41, 28, 28);
            color: white;
        }
    }

    .Carousel-con1{
        padding-top: 2rem;

        .studentcard-con{
            padding-right: 1rem;
           
            img{
                height: 110px;
            }
            p{
                font-size: 14px;
                padding: 0;
                width: 100%;
                .icons{
                    color: gold;
                }
            }

            a{
               font-size: 14px;
               padding: .2rem;
               border: 1px solid rgb(38, 22, 22);
               background-color: gold;
            }
        }

    }
}

@media only screen and (max-width: 800px) {
   
    .Coures-card-container{
        padding: .5rem;
        margin-top: .5rem;
        .h1{
            display: none;
        }
       .p{
            display: none;
        }
        .a{
            display: none;
        }


        .Carousel-con1{
            padding-top: 0rem;
    
            .studentcard-con{
                padding-right: .3rem;
    }
}
}


}


@media only screen and (max-width: 400px) {
   
    .Coures-card-container{
        padding: .2rem;
        margin-top: .2rem;
      


        .Carousel-con1{
            padding-top: .2rem;
    
            .studentcard-con{
                padding-right: .1rem;
    }
}
}


}