:root{
    --primaryColor : hsl(199, 100%, 33%);
    --secondaryColor : hsl(187, 85%, 43%);
    --gardientColor : linear-gradient(to right,hsl(187, 85%, 43%),hsl(199, 100%, 33%));
    --whiteColor:  hsl(0, 0%, 100%);
    --blackColor: hsl(201, 33%, 16%);
    --textColor: hsl(240, 4%, 36%);
    --whiteColorDeam:  hsl(0, 0%, 93%);
    --grayText : rgb(190, 190, 190);
    --inputColur: rgb(239, 239, 239);
    --bodyColor: rgb(240, 240, 246);
    --cardBG: rgb(225, 225, 235);
  }




.nav-sec{
    width: 100%;
    padding: 1rem 3rem;
    background: var(--whiteColor);
    border-bottom: 1px solid var(--grayText);
    .navbar-div{
        width: 100%;
        justify-content: space-evenly;
      
        
      .navbar-item1{
        display: none;
      }
          

      .navbar-item2{
        a{
          h1{
            font-size: 20px;
          }
        }
      }
      .navbar-item3{
        width: 40%;
        border: 1px solid black;
        border-radius: 3rem;
       
        .icons1{
          margin-top: 1rem;
          margin-left: 1rem;
          font-size: 14px;

          
        }
       
        input{

            width: 90%;
            border-radius: 3rem;
            padding: .5rem;
            outline: none;
            border: none;

            
            
            &::placeholder{
                color: var(--blackColor);
                font-size: 15px;
                align-items: center;
                display: grid;
                padding-left: .5rem;
                
                
            }
            

        }
      }
      .navbar-item4{
        a{
          .icon{
            &:hover{
              color: var(--primaryColor);
            }
          }
          .icons1{
            display: none;

            
              &:hover{
                color: var(--primaryColor);
              }
             
          }
        }
      }

       .navbar-item5{
        font-size: 14px;
       a{
        &:hover{
          color: var(--primaryColor);
        }
       }
       }
     
       .navbar-item6{
        font-size: 14px;
        a{
          &:hover{
            color: var(--primaryColor);
          }
         }
       }
       .navbar-item10{
        font-size: 14px;
        a{
          &:hover{
            color: var(--primaryColor);
          }
         }
       }
     
      .navbar-item7{
        
        button{
        

            border: 1px solid black;
        color: black;
        background: var(--whiteColor);
        padding:.5rem 1rem;
        &:hover{
           background: var(--whiteColorDeam);
        }
        }
      }

      .navbar-item8{
        
        button{
            border: 1px solid black;
            color: white;
            background: var(--blackColor);
            padding:.5rem 1rem;
     

        &:hover{
           color: var(--grayText);
        }
        }
      }
      
      .navbar-item9{
        a{
          .icons{
            font-size: 25px;
            &:hover{
              color: var(--primaryColor);
            }
          }
        }
      }
      
      }

     
      }




      @media only screen and (max-width: 1200px) {
        .nav-sec{
          .navbar-div{
            .navbar-item10{
                 display: none;
            }
          }
        }
      }

      @media only screen and (max-width: 960px) {
        .nav-sec{
          .navbar-div{
            .navbar-item6{
                 display: none;
            }
          }
        }
      }

      @media only screen and (max-width: 850px) {
        .nav-sec{
          padding: 1rem 1.5rem;
          .navbar-div{
            justify-content: space-between;
            .navbar-item1{
              display: block;
              }
            .navbar-item3{
                 display: none;
            }
            .navbar-item7{
              display: none;
         }
         .navbar-item8{
          display: none;
          }
          .navbar-item9{
            display: none;
            }

            .navbar-item5{
              display: none;
              }
              .navbar-item4{
                a{
                  .icons1{
                    display: inline;
                    padding-right: .5rem;
                    font-size: 20px;
                  }
                }
              }
              
          }
        }
      }

      @media only screen and (max-width: 400px) {
        .nav-sec{
          .navbar-div{
            justify-content: space-between;
            .navbar-item1{
              a{
                .icon{
                  font-size: 20px;
                }
              }
              }
              .navbar-item{
                a{
                  h1{
                    font-size: 20px;
                  }
                }
               
              }
              .navbar-item4{
                a{
                  .icons1{
                    display: inline;
                    padding-right: .5rem;
                    font-size: 20px;

                  }
                  .icon{
                    font-size: 20px;
                  }
                }
              }
              
          }
        }
      }


      @media only screen and (max-width: 200px) {

        .nav-sec{
          .navbar-div{
            .navbar-item4{
                 display: none;
            }
          }
        }
      }
      