.Greatteams-sec{
    padding: 3rem;
    padding-top: 6rem;

    .teams-conatiner{
        padding: 1rem;
        text-align: center;
        h3{
            padding-bottom: .3rem;
        }
        p{
            padding-bottom: .3rem;

        }
    }

    .teams-logos{
        grid-template-columns: repeat(6, 1fr);
        row-gap: 1rem;
        justify-content: space-between;
        img{
            height: 40px;
        }
    }



    .teamimges{
        padding-top: 2rem;

        .teamimges-sec1{
             
             img{
                max-height: 600px;
                padding: .2rem;

             }


        }


        .teamimges-sec2{
            display: none;
             
            img{
               max-height: 600px;
               padding: .2rem;

            }


       }
        .bussdiv{
            padding: .2rem 2rem;
            p{
                text-align: center;
                padding: 1rem;
                font-size: 15px;
            }
         button{
            width: 100%;
            background-color: black;
            color: white;
            padding: 1rem 1rem;
            justify-content: center;
         }
        img{
            height: 100px;
            max-width: 300px;
            margin: auto;
              

        }
        h1{
            text-align: center;
            font-size: 20px;
        }

        ul{
            padding-top: 1rem;
            padding-bottom: 1rem;
            li{
                list-style: disc;
            }
        }
        .bus-btns{
            width: 100%;
            display: flex;
            justify-content: space-evenly;
            gap: 1rem;
            

            a{
                border: 1px solid black;
                width: 100%;
                text-align: center;
                padding: .3rem;
            }
            .a1{
                background-color: rgb(8, 8, 8);
                color: white;
               
                &:hover{
                background-color: rgb(108, 102, 102);

                    color: white;
                }
            }
            .a2{
                &:hover{
                    background-color: black;
                    color: white;
                }
            }
          
        }
 
        }

    }
}

@media screen and (max-width: 600px){
    .Greatteams-sec{
    

    .teamimges{
        
           

        .bussdiv{
             
            button{
                width: 100%;

            }
            .bus-btns{
                display: grid;
                justify-content: space-between;
                width: 100%;

                a{
                    width: 100%;
                    padding: .2rem 3rem;
                }
            }

        }

    }
    
    
       
        
    }
    }


@media screen and (max-width: 960px){
.Greatteams-sec{




    .teams-logos{
        grid-template-columns: repeat(3, 1fr);
    

}

    
}
}

@media screen and (min-width: 800px){
    .Greatteams-sec{
    
    
        .teamimges{
            grid-template-columns: repeat(2, 1fr);
            max-width: 870px;
            margin: auto;
            padding-top: 6rem;
            
            .teamimges-sec1{
                display: none;
            }

            .teamimges-sec2{
                display: block;
            }

        
        }
    
    
        
    }
    }