
:root{
    --primaryColor : hsl(199, 100%, 33%);
    --secondaryColor : hsl(187, 85%, 43%);
    --gardientColor : linear-gradient(to right,hsl(187, 85%, 43%),hsl(199, 100%, 33%));
    --whiteColor:  hsl(0, 0%, 100%);
    --blackColor: hsl(201, 33%, 16%);
    --textColor: hsl(240, 4%, 36%);
    --whiteColorDeam:  hsl(0, 0%, 93%);
    --grayText : rgb(190, 190, 190);
    --inputColur: rgb(239, 239, 239);
    --bodyColor: rgb(240, 240, 246);
    --cardBG: rgb(225, 225, 235);
  }
  

.Goals{
    padding: 2rem 3rem;
    background: var(--whiteColorDeam);
    h1{
        font-size: 20px;
    }

  
   .carousel-div{
    
    padding: 2rem 0;
 
    
        .card-goal{
            padding: 1rem;
            border: 1px solid black;
            background: var(--whiteColor);
            width: 97%;
            height: 360px;

            
            .icon-div{  
                padding-left: 1rem;
                padding-top: 1rem;
            }


            p{
                padding: 1rem;
                height: 160px;
                overflow: hidden;
            }
            h4{
                padding: 1rem;
                border-bottom: 1px solid black;

                span{
                    background: var(--blackColor);
                    color: var(--whiteColor);
                    padding: .3rem .5rem;
                    border-radius: 3rem;
                    margin-right: 1rem;
                }
            }
            
            .card-footer{
                padding: .2rem 1rem;
                height: 50px;
                overflow: hidden;
                color: blue;
                &:hover{
                    color: rgb(25, 25, 119);
                }

                .icons{
                    font-size: 30px;
                    float: left;
                    margin-right: 1rem;
                }
                a{
                    h3{
                     color: rgb(77, 77, 203);
                      font-size: 15px;
                      overflow: hidden;
                      &:hover{
                        color: rgb(25, 25, 119);
                    }
                    }
                  
                }
            }
          
        }
    }
    

}


@media only screen and (min-width: 1240px) {
    
    .Goals{
        h1{
            font-size: 15px;
        }
        .carousel-div{
        .card-goal{
           height: 360px;

        

        h4{
            font-size: 15px;
        }
         p{
            font-size: 12px;
         }

        }


        .card-footer{
           
           

            .icons{
                font-size: 25px;
               
            }
            a{
                h3{
                  font-size: 10px;
                  &:hover{
                    color: rgb(25, 25, 119);
                }
                }
              
            }
        }
    }
    }
    
    }


    @media only screen and (max-width: 400px) {
    
        .Goals{

            h1{
                font-size: 18px;
            }
           
          

            .carousel-div
            .card-goal{
              min-width: 250px;
            


              h4{
                font-size: 15px;
            }
             p{
                font-size: 13px;
             }
    
            }
            }
            
        }
        
        
        @media only screen and (max-width: 300px) {
    
            .Goals{
                display: none;
            }
        }