
.student-card-container{

    padding: 2rem 3rem;
    h1{
        padding: 1rem 0;
    }
   .Carousel-con{

    .studentcard-con{
        margin-right: .5rem;
        height: 350px;

        img{
            background-color: gray;
            height: 110px;

            
        }
        p{
            overflow: hidden;
        }
       
        p{
            .icons{
                color: goldenrod;
            }
        }
        a{
            background-color: gold;
            border: none;
            padding: .2rem;
        }


        &:hover{
            background-color: aliceblue;
        }
    }


   }
}



@media only screen and (max-width: 600px) {
   
    .student-card-container{
    

        h1{
            font-size: 25px;
        }
      


       
}


}



@media only screen and (max-width: 400px) {
   
    .student-card-container{
        padding: .4rem;
        margin-top: .3rem;

        h1{
            font-size: 15px;
        }
      


        .Carousel-con{
            padding: 1rem;
    
            .studentcard-con{
                padding-right: .3rem;
    }
}
}


}