:root{
    --primaryColor : hsl(199, 100%, 33%);
    --secondaryColor : hsl(187, 85%, 43%);
    --gardientColor : linear-gradient(to right,hsl(187, 85%, 43%),hsl(199, 100%, 33%));
    --whiteColor:  hsl(0, 0%, 100%);
    --blackColor: hsl(201, 33%, 16%);
    --textColor: hsl(240, 4%, 36%);
    --whiteColorDeam:  hsl(0, 0%, 93%);
    --grayText : rgb(190, 190, 190);
    --inputColur: rgb(239, 239, 239);
    --bodyColor: rgb(240, 240, 246);
    --cardBG: rgb(225, 225, 235);
  }


.Topcate-conatiner{
    padding: 2rem 3rem;
    background: var(--whiteColor);
    width: 100%;
    height: max-content;
      

    h1{
        padding-bottom: 2rem;
    }


    .top-candiv{
        width: 100%;
        gap: 1rem;
        grid-template-columns: repeat(4, 1fr);
        justify-content: space-between;
        
       
        .cate-card{
            width: 100%;
            height: max-content;
            img{
           

                transition: .9s ease;

                }
                &:hover{
                    img{
                        transform: scale(1.05);
                    }
                }
           h5{
            padding-top: .3rem;
           }
        }

    }
}



@media  (max-width : 700px) {

    .Topcate-conatiner{
        h1{
            font-size: 20px;
        }

        .top-candiv{
        grid-template-columns: repeat(3, 1fr);


        
              
        }

    }
}

@media  (max-width : 600px) {

    .Topcate-conatiner{

        .top-candiv{
            img{
                display: none;

            }
            h5{
                padding: .5rem;
                border: 1px solid black;
                border-radius: 3rem;
                width: max-content;
                font-size: 14px;
            }
              
        }

    }
}



@media  (max-width : 500px) {
    
    .Topcate-conatiner{

        .top-candiv{
           grid-template-columns: repeat(2, 1fr);

            img{
                display: none;

            }
            h5{
                padding: .5rem;
                border: 1px solid black;
                border-radius: 3rem;
                width: max-content;
                font-size: 16px;
            }
              
        }

    }
}    

@media  (max-width : 410px) {
    
   .Topcate-conatiner{
       padding: 1rem 0;
     
   
       .top-candiv{
                 
          h5{
                    font-size: 12px;
                }
           }

   }
}   

@media  (max-width : 320px) {
  
   .Topcate-conatiner{
       padding: 1rem 0;
      
       .top-candiv{
                 
        h5{
                  font-size: 10px;
              }
         }

   }
}  