


.Footer-container{
    background-color: rgb(15, 9, 40);
   
    padding: 2rem 3rem;

    .Footer-con{
        grid-template-columns: repeat(2, 1fr);
    justify-content: space-between;

    .footer-con2{
        
    grid-template-columns: repeat(3, 1fr);
    justify-content: space-between;

        .con2-item{
           button{
            display: none;
           }
            a{
                color: white;
                padding-top: .5rem;
                font-size: 14px;
                &:hover{
                    color: wheat;
                    text-decoration: underline;
                }
            }
        }

     
        
    }

    .footer-con3{
        position: relative;

        

        button{
            position: absolute;
            right: 2rem;
            bottom: 2rem;
            padding: .3rem .5rem;
            width: max-content;
            
        }
    }
}
    .footer-end{
        width: 100%;
        display: flex;
        justify-content: space-between;

        .footerend-con{
            padding-top: 2rem;
            img{
                height: 40px;
                color: white;
            }
            a{
                color: white;
            }
        }
    }
}





@media only screen and (max-width: 600px) {
  .Footer-container{

    .Footer-con{
    grid-template-columns: repeat(1, 1fr);


    .footer-con2{
        
        grid-template-columns: repeat(1, 1fr);

      
            .con2-item{
                button{
                 display: block;
                 padding: .3rem .5rem;
                 width: max-content;
                }
            
       
     
  }

 
}
.footer-con3{
    display: none;

    
 }
}


 .footer-end{
    width: 100%;
    display: grid;

    img{
           width: max-content;
    }
}



}
}