.topimg{
   
    .imgdiv{
        width: 100%;
        height: 400px;
        background-color: white;
        position: relative;
    
        img{
            width: 100%;
            height: 400px;
            object-fit: cover;
        }
    
    
        .top-text-bar{
            position: absolute;
            top: 30px;
            left: 60px;
    
            width: 400px  ;
            height: max-content;
            background-color: white;
            padding: 2rem;
            box-shadow: 0 0 5px black;
    
        }
    
    }
}



@media  (max-width :800px) {

    .topimg{
    .imgdiv{
      
        .top-text-bar{
            display: none;
        }
        
    
    }
}
    
}